// import "../styles/Navbar.css";



function Navbar(){
  function handleClick(event){
    console.log(event.target);

    const navLinks = document.querySelectorAll(".nav-link")
    console.log(navLinks);
    navLinks.forEach( (navLink) => {
      navLink.classList.remove("active");
    });

    event.target.classList.add("active");

  }

    return(
        <nav className="navbar is-visible navbar-expand-lg navbar-dark bg-dark">
  <div className="container-fluid">
    <a className="navbar-brand" href="#header">William Farre</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse " id="navbarNav">
      <ul className="navbar-nav ms-auto">
        <li className="nav-item">
          <a onClick={handleClick} id="home-link" className="nav-link active" aria-current="page" href="#header">Home</a>
          <div className="underline"></div>
        </li>
        <li className="nav-item">
          <a  onClick={handleClick} id="about-link" className="nav-link" href="#about">About me</a>
          <div className="underline"></div>
        </li>
        <li className="nav-item">
          <a  onClick={handleClick} id="portfolio-link" className="nav-link" href="#portfolio">Portfolio</a>
          <div className="underline"></div>
        </li>
        <li className="nav-item">
          <a onClick={handleClick} id="contact-link" className="nav-link" href="#contact">Contacts</a>
          <div className="underline"></div>
        </li>
      </ul>
    </div>
  </div>
</nav>
    )
}


export default Navbar;